<!--  -->
<template>
  <div>
    <div id="allmap"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  watch: {
    coord(newVal,oldVal){
      this.initMap();
    }
  },
  components: {},

  computed: {
    coord(){
      return this.$store.state.selected_boat.coord;
    }
  },

  mounted() {
    this.initMap();
  },

  methods: {
    initMap() {
      var map = new BMap.Map("allmap"); // 创建Map实例
      var coord_x=this.coord[0];
      var coord_y=this.coord[1];
      var point = new BMap.Point(coord_x,coord_y);
      map.centerAndZoom(point, 18); // 初始化地图,设置中心点坐标和地图级别
      //添加地图类型控件
      map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
        })
      );
      map.setCurrentCity("浙江"); // 设置地图显示的城市 此项是必须设置的
      map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      var marker = new BMap.Marker(point); // 创建标注
      map.addOverlay(marker); // 将标注添加到地图中
      marker.setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
      var label = new BMap.Label("小船当前位置", {
        offset: new BMap.Size(-30, 30)
      });
      marker.setLabel(label);
    }
  }
};
</script>
<style scoped>
#allmap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  position: absolute;
}
</style>