<!--  -->
<template>
  <div class="changeboat" style="height:100%;">
    <el-drawer
      ref="drawer"
      title="请选择您的小船"
      :visible.sync="drawer_flag"
      :direction="direction"
      :before-close="handleClose"
      style="overflow:none;"
    >
      <hr/>
      <div style="height:68%;overflow:auto">
        <div v-for="boat in boatmsg" :key="boat.id">
          <queryboat
            :boat="boat"
            @closeDrawer="closeDrawer"
          ></queryboat>
          <hr/>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import QueryBoat from "./QueryBoat";
export default {
  data() {
    return {
      direction: "ltr",
    };
  },
  components: {
    queryboat: QueryBoat
  },

  computed: {
    drawer_flag: {
      get() {
        return this.$store.state.drawer;
      },
      set(val) {
        this.$store.state.drawer = val;
      }
    },
    boatmsg(){
      return this.$store.state.boatmsg;
    }
  },

  mounted() {},

  methods: {
    handleClose(done) {
      this.$store.commit("closeDrawer");
      done();
    },
    closeDrawer(){
      this.$refs.drawer.closeDrawer();
    }
  }
};
</script>
